"use client";

import React, { useState, useEffect } from 'react';
import { services } from '@/services/api';
import { useAuth } from '@/hooks/useAuth';
import toast from 'react-hot-toast';
import Button from '@/components/ui/Button';
import SearchInput from '@/components/form/input/SearchInput';
import UserCard from '@/components/user/UserCard';
import UserForm from '@/components/user/UserForm';
import { Modal } from '@/components/ui/modal';

type User = {
  id: number;
  nome: string;
  email: string;
  celular: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
};

type CreateUserRequest = {
  nome: string;
  email: string;
  senha: string;
  celular?: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
};

export default function UsuariosPage() {
  const { user: currentUser } = useAuth();
  const [users, setUsers] = useState<User[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedUser, setSelectedUser] = useState<User | null>(null);

  const loadUsers = async () => {
    try {
      setLoading(true);
      const response = await services.users.list();
      setUsers(response.data);
    } catch (error) {
      toast.error('Erro ao carregar usu치rios');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadUsers();
  }, []);

  const handleEdit = (user: User) => {
    setSelectedUser(user);
    setIsModalOpen(true);
  };

  const handleDelete = async (id: number) => {
    if (!window.confirm('Tem certeza que deseja excluir este usu치rio?')) return;

    try {
      await services.users.delete(id);
      toast.success('Usu치rio exclu칤do com sucesso');
      loadUsers();
    } catch (error) {
      toast.error('Erro ao excluir usu치rio');
    }
  };

  const handleSave = async (data: any) => {
    console.log('handleSave:', data);
    try {
      if (selectedUser) {
        // Para atualiza칞칚o, removemos campos undefined e senha se estiver vazia
        const updateData = Object.fromEntries(
          Object.entries({
            nome: data.nome,
            email: data.email,
            celular: data.celular,
            role: data.role
          }).filter(([_, value]) => value !== undefined)
        );

        await services.users.update(selectedUser.id, updateData);
        toast.success('Usu치rio atualizado com sucesso');
      } else {
        // Para cria칞칚o, garantimos que todos os campos obrigat칩rios estejam presentes
        const createData: CreateUserRequest = {
          nome: data.nome,
          email: data.email,
          senha: data.senha,
          role: data.role || 'VENDEDOR',
          celular: data.celular
        };

        await services.users.create(createData);
        toast.success('Usu치rio criado com sucesso');
      }
      setIsModalOpen(false);
      setSelectedUser(null);
      loadUsers();
    } catch (error) {
      throw error; // Delega o tratamento de erro para o UserForm
    }
  };

  const filteredUsers = users.filter(user =>
    user.nome.toLowerCase().includes(searchTerm.toLowerCase()) ||
    user.email.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-8">
        <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
          <div>
            <h1 className="text-2xl font-semibold text-gray-900 dark:text-white">
              Usu치rios
            </h1>
            <p className="mt-1 text-sm text-gray-500 dark:text-gray-400">
              Gerencie os usu치rios do sistema
            </p>
          </div>
          <Button
            variant="primary"
            onClick={() => {
              setSelectedUser(null);
              setIsModalOpen(true);
            }}
          >
            Adicionar Usu치rio
          </Button>
        </div>

        <div className="mt-4 w-full sm:max-w-xs">
          <SearchInput
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            placeholder="Buscar por nome ou email..."
          />
        </div>
      </div>

      {loading ? (
        <div className="flex justify-center items-center min-h-[200px]">
          <div className="loader" />
        </div>
      ) : (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {filteredUsers.map(user => (
            <UserCard
              key={user.id}
              user={user}
              onEdit={() => handleEdit(user)}
              onDelete={() => handleDelete(user.id)}
              isCurrentUser={currentUser?.id === user.id}
            />
          ))}
        </div>
      )}
      <Modal isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false);
          setSelectedUser(null);
        }} className="max-w-[800px] m-4">
        <div className="no-scrollbar relative w-full overflow-y-auto rounded-3xl bg-white p-4 dark:bg-gray-900 lg:p-11">
          <div className="px-2 pr-14">
            <h4 className="mb-2 text-2xl font-semibold text-gray-800 dark:text-white/90">
              {selectedUser ? 'Editar Usu치rio' : 'Novo Usu치rio'}
            </h4>
            <p className="mb-6 text-sm text-gray-500 dark:text-gray-400 lg:mb-7">
              {selectedUser ? 'Atualize as informa칞칫es do usu치rio.' : 'Preencha os dados para criar um novo usu치rio.'}
            </p>
          </div>
          <UserForm
            user={selectedUser}
            onSubmit={handleSave}
            onCancel={() => {
              setIsModalOpen(false);
              setSelectedUser(null);
            }}
          />
        </div>
      </Modal>
    </div>
  );
}
