import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import Input from '@/components/form/input/InputField';
import Label from '@/components/form/Label';
import Button from '@/components/ui/Button';
import { useAuth } from '@/hooks/useAuth';
import Select from '../ui/Select';

type User = {
  id: number;
  nome: string;
  email: string;
  celular: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
};

type FormErrors = {
  nome?: string;
  email?: string;
  senha?: string;
  celular?: string;
  role?: string;
};

type UserFormData = {
  nome: string;
  email: string;
  senha?: string;
  celular?: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
};
type ErrorOnSubmitDetails = {
  field: string;
  message: string;
}
type ErrorOnSubmit = {
  response?: {
    data?: {
      details?: ErrorOnSubmitDetails[];
    };
  };
}

const roleOptions = [
  // { value: "SUPERADMIN", label: "Super Administrador" },
  // { value: "ADMIN", label: "Administrador" },
  { value: "GESTOR", label: "Gestor" },
  { value: "VENDEDOR", label: "Vendedor" },
  { value: "CLIENTE", label: "Cliente" }
];

type UserFormProps = {
  user?: User | null;
  onSubmit: (data: UserFormData) => Promise<void>;
  onCancel: () => void;
};

const roles = [
  // { value: 'ADMIN', label: 'Administrador' },
  { value: 'GESTOR', label: 'Gestor' },
  { value: 'VENDEDOR', label: 'Vendedor' },
  { value: 'CLIENTE', label: 'Cliente' }
];

const UserForm: React.FC<UserFormProps> = ({ user, onSubmit, onCancel }) => {
  const { user: currentUser } = useAuth();
  const [formData, setFormData] = React.useState<UserFormData>({
    nome: user?.nome || '',
    email: user?.email || '',
    celular: user?.celular || '',
    role: user?.role || 'GESTOR',
  });
    const [errors, setErrors] = useState<FormErrors>({});
    const [isSubmitting, setIsSubmitting] = useState(false);
  // const { handleSubmit, formState: { errors, isSubmitting } } = useForm<UserFormData>({
  //   defaultValues: user || {
  //     nome: '',
  //     email: '',
  //     celular: '',
  //     role: 'GESTOR'
  //   }
  // });

  const isEditing = !!user;
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setIsSubmitting(true);
    try {
      await onSubmit(formData);
    } catch (error: any) {
      const erros = error.response?.data?.details || [];
      erros.forEach((err: any) => {
        const field = err.field.replace('body.', '');
        setErrors(prev => ({ ...prev, [field]: err.message }));
      });
    } finally {
      setIsSubmitting(false);
    }
  };


  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <Label htmlFor="nome">
          Nome Completo<span className="text-error-500">*</span>
        </Label>
        <Input
          id="nome"
          name='nome'
          type="text"
          placeholder="Digite seu nome completo"
          required
          defaultValue={user?.nome || ''}
          onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
          error={errors.nome}
        />
      </div>

      <div>
        <Label htmlFor="email">
          E-mail<span className="text-error-500">*</span>
        </Label>
        <Input
          id="email"
          name='email'
          type="email"
          placeholder="Digite seu email"
          required
          defaultValue={user?.email || ''}
          onChange={(e) => setFormData({ ...formData, email: e.target.value })}
          error={errors.email}
        />
      </div>

      {!isEditing && (
        <div>
          <Label htmlFor="senha">
            Senha{!isEditing && <span className="text-error-500">*</span>}
          </Label>
          <Input
            id="senha"
            name='senha'
            type="password"
            placeholder="Digite sua senha"
            min={6}
            required
            onChange={(e) => setFormData({ ...formData, senha: e.target.value })}
            error={errors.senha}
          />
        </div>
      )}

      <div>
        <Label htmlFor="celular">Celular</Label>
        <Input
          id="celular"
          name='celular'
          required
          type="tel"
          placeholder="(00) 00000-0000"
          defaultValue={user?.celular || ''}
          onChange={(e) => setFormData({ ...formData, celular: e.target.value })}
          error={errors.celular}
        />
      </div>

      <div>
        <Label htmlFor="role">
          Fun칞칚o<span className="text-error-500">*</span>
        </Label>

        <Select
          id="role"
          name="role"
          value={roleOptions.find(option => option.value === formData.role)}
          options={roleOptions}
          onChange={(option) => setFormData({ ...formData, role: (option as { value: UserFormData['role']; label: string }).value })}
          isSearchable
          placeholder="Selecione o tipo de usu치rio"
          error={errors.role}
          className="w-full"
        />
      </div>

      <div className="flex justify-end space-x-4 pt-4">
        <Button
          variant="secondary"
          onClick={onCancel}
          type="button"
        >
          Cancelar
        </Button>
        <Button
          variant="primary"
          type="submit"
          loading={isSubmitting}
        >
          {isEditing ? 'Atualizar' : 'Criar'}
        </Button>
      </div>
    </form>
  );
};

export default UserForm;
