import React from 'react';
import { UserCircleIcon, EditIcon, TrashBinIcon } from '@/icons';
import Button from '../ui/Button';

type User = {
  id: number;
  nome: string;
  email: string;
  celular: string;
  role: 'SUPERADMIN' | 'ADMIN' | 'GESTOR' | 'VENDEDOR' | 'CLIENTE';
};

type UserCardProps = {
  user: User;
  onEdit: () => void;
  onDelete: () => void;
  isCurrentUser: boolean;
};

const roleColors = {
  SUPERADMIN: 'bg-purple-100 text-purple-800 dark:bg-purple-900/20 dark:text-purple-300',
  ADMIN: 'bg-blue-100 text-blue-800 dark:bg-blue-900/20 dark:text-blue-300',
  GESTOR: 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-300',
  VENDEDOR: 'bg-orange-100 text-orange-800 dark:bg-orange-900/20 dark:text-orange-300',
  CLIENTE: 'bg-gray-100 text-gray-800 dark:bg-gray-900/20 dark:text-gray-300'
};

const roleNames = {
  SUPERADMIN: 'Super Admin',
  ADMIN: 'Administrador',
  GESTOR: 'Gestor',
  VENDEDOR: 'Vendedor',
  CLIENTE: 'Cliente'
};

const UserCard: React.FC<UserCardProps> = ({ user, onEdit, onDelete, isCurrentUser }) => {
  return (
    <div className="bg-white dark:bg-gray-800 rounded-lg shadow-sm hover:shadow-md shadow-gray-200 dark:shadow-gray-700 border border-gray-200 dark:border-gray-700 p-4">
      <div className="flex items-start justify-between">
        <div className="flex items-center space-x-3">
          <div className="bg-gray-100 dark:bg-gray-700 rounded-full p-2">
            <UserCircleIcon className="text-gray-500 dark:text-gray-400" />
          </div>
          <div>
            <h3 className="text-sm font-medium text-gray-900 dark:text-white">
              {user.nome}
              {isCurrentUser && (
                <span className="ml-2 text-xs text-gray-500 dark:text-gray-400">
                  (Voc칡)
                </span>
              )}
            </h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">{user.email}</p>
          </div>
        </div>
        <span className={`px-2.5 py-0.5 text-xs font-medium rounded-full ${roleColors[user.role]}`}>
          {roleNames[user.role]}
        </span>
      </div>

      <div className='flex justify-between items-center'>
        {user.celular && (
        <div className="mt-3 text-sm text-gray-500 dark:text-gray-400">
          游님 {user.celular}
        </div>
      )}

      {/* </div> */}
      <div className="mt-4 flex space-x-2">
          <Button
            variant="secondary"
            size="xs"
          onClick={onEdit}
              className="flex items-center justify-center text-gray-400 hover:text-brand-500 dark:hover:text-brand-400 transition-colors"
          title='Editar Usu치rio'
        >
          <EditIcon className="w-5 h-5 inline-block" />
        </Button>
        {!isCurrentUser && (
          <Button
            variant="secondary"
            size="xs"
            onClick={onDelete}
            className="flex items-center justify-center text-red-500 dark:text-red-400 rounded-md transition-colors"
            title='Excluir Usu치rio'
          >
            <TrashBinIcon className="w-5 h-5 inline-block" />
          </Button>
        )}
      </div>
      </div>

    </div>
  );
};

export default UserCard;
